<template>
	<div v-if="jj_visible" class="jj-dialog">
		<div class="messagebox-shade" :style="{'background-color':maskColor}"  @click="touchClose?close():''">
			<div class="messagebox-main popIn" @click="mainClick" :style="{'animation-duration':duration+'s','width':width,'background':background,'border-radius':radius+'px'}">
				<div class = "background-content">
					<slot name="backgroundContent"></slot>
				</div>
				<div class="messagebox-content" :style="{'padding':padding}">
					<div v-if="showClose" class="rightTopClose"  @click="close">
						<div class="closeImage" :style="closeStyle"> </div>
					</div>
					<div v-if="title.length > 0" class="flexCenter" :style="titleStyle"><span>{{title}}</span></div>
					<div v-if="message.length > 0" class="flexCenter" style="margin-top: 10px;" :style="messageStyle"><span>{{message}}</span></div>
					<slot></slot>
				</div>
				<slot name="footer"></slot>
			</div>
		</div>
	</div>

</template>

<script>
	export default {
		name: 'jj-dialog',//index.js
		props: {
			visible: {
				//
				type: Boolean,
				default: false,
			},
			duration:{
				//
				type: Number,
				default: 0.25,
			},
			radius:{
				//
				type: Number,
				default: 5,
			},
			background:{
				//
				type: String,
				default: "#fff",
			},
			maskColor: {
				//
				type: String,
				default: "rgba(0, 0, 0, 0.35)",
			},
			touchClose: {
				//
				type: Boolean,
				default: false,
			},
			showClose: {
				//
				type: Boolean,
				default: true
			},
			closeStyle: {
				//
				type: Object,
				default: function(){
					return {}
				}
			},
			title: {
				//
				type: String,
				default: ''
			},
			titleStyle: {
				//
				type: Object,
				default: function(){
					return {}
				}
			},
			message: {
				//
				type: String,
				default: ''
			},
			messageStyle: {
				//
				type: Object,
				default: function(){
					return {}
				}
			},
			width: {
				////px,300px
				type: String,
				default: '80%'
			},
			padding: {
				type: String,
				default: '20px'
			}
		},
		watch: {
			visible: {
				handler(newVal) {
					this.jj_visible = newVal
				},
				immediate: true
			},
		},
		data() {
			return {
				jj_visible: this.visible
			}
		},
		computed: {},
		methods: {
			close() {
				this.$emit('close')
				this.jj_visible = false
			},
			mainClick(e) {
				//
				e.stopPropagation()
			},
		}
	}
</script>

<style scoped>
	@import "../jj-messagebox.css";
	@import "../jj-pop.css";
	.jj-dialog{
		display: flex;
		flex-direction: column;
	}
</style>
