import Vue from 'vue';
import loading from './jj-loading.vue';
let jjLoading = Vue.extend(loading); //vm

let jj_loading_close = function() {
	let loadingElement = document.getElementById('jj-loading-id')
	if (loadingElement) {
		//
		loadingElement.remove()
	}
}

export let getData = function(loadingData) {
	let data = {}
	let isLoadingData = loadingData === undefined || loadingData === null
	if (!isLoadingData){
		if (loadingData.constructor === Object) {
			if (loadingData.isClose === true) {
				return null
			}
			data = {
				...loadingData
			}
		} else {
			data = {
				"message": loadingData + '',
			}
		}
	}
	return data
}

let jj_loading = function(loadingData) {
	let data = getData(loadingData)
	if (data === null) {
		//
		jj_loading_close()
		return
	}
	//
	jj_loading_close()

	let instance = new jjLoading({
		data
	})
	/*
	installedtruealertinstall
	Vue.prototype.$jj_loading = jjLoading.installinstalled
	this.$jj_loading
	*/
	if (jjLoading.installed) {
		instance.$mount()
		document.body.appendChild(instance.$el)
	}
	jjLoading.installed = true
	return instance
}

jjLoading.install = function(data) {
	return jj_loading(data)
}
Vue.prototype.$jj_loading = jj_loading
export default jjLoading
